#blog-page {
    flex-direction: column;
    
    .blog-list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
        gap: 3rem;
        width: 100%;
        place-content: center;
        padding: 0 2rem 5rem;
        .dummy-image-container {
            
            img {
                width: 100%;
            }
        }
        .post {
            padding: 2rem;
            flex-direction: column;
            height: 300px;
            row-gap: 1rem;
            background-color: #fff;
            border-radius: .5rem;
            cursor: pointer;
            box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
            transition: .15s ease-in-out;
            .tags {
                width: 100%;
                justify-content: space-evenly;
            }
            .title {
                text-align: center;
            }
            .reactions {
                gap: .5rem;
            }
            &:hover {
                background-color: rgb(255, 251, 243);
            }
        }
    }
}

#post {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.658);
    top: 0;
    left: 0;
    z-index: 11;
    .post-container {
        display: flex;
        background-color: white;
        border-radius: 2rem;
        flex-direction: column;
        max-width: 700px;
        overflow-y: scroll;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 80vw;
        height: 90%;
        padding: 1rem;
        max-height: fit-content;
        &::-webkit-scrollbar {
            width: 0px;
        }
        &::-webkit-scrollbar-track {
            width: 0px;
        }
        &::-webkit-scrollbar-thumb {
            width: 0px;
        }
    }
    
    .exit-post {
        z-index: 2;
        border: none;
        background-color: #000;
        border-top-right-radius: 2rem;
        border-bottom-left-radius: 2rem;
        padding: 1rem;
        color: #fff;
        cursor: pointer;
        position: absolute;
        top: 0%;
        right:0;
        transition: .11s ease-out;
        text-decoration: none;
        &:hover {
            font-size: 1.3rem;
            background-color: orange;
        }
    }
    .title {
        padding: 1rem 0;
    }
    .post-pic {
        position: relative;
        min-height: 450px;
        max-height: 450px;
        img {
            max-width: 100%;
            width: 100%;
            height: 100%;
            max-height: 100%;
            object-fit: cover;  
        }
        .img-loader {
            p {
                position: absolute;
                top: 83%;
                left: 50%;
                transform: translate(-50%, -30%);
                width: 75%;
                text-align: center;
                color: #c52020;
                font-weight: bold
            }
        }
    }
    .tags {
        padding: 1rem;
        width: 100%;
        justify-content: space-around;
    }
    .comments {
        display: flex;
        flex-direction: column;
        row-gap: 1.25rem;
        width: 100%;
        padding: 1rem;
        background: #f5f5f5;
        border-radius: 1.25rem;
        border: 1px solid #ebebeb;
        .post-input {
            flex-direction: column;
            width: 100%;
            row-gap: 1rem;
            position: relative;
            input {
                width: 100%;
                border: none;
                box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
                padding: .5rem;
                border-radius: .25rem;
                font-size: 1rem;
            }
            .comment-info {
                position: absolute;
                right: 10px;
                top: 8px;
                z-index: 2;
                svg {
                    height: 22px;
                    width: 22px;
                    cursor: help;
                }
            }
            .info-text {
                z-index: 1;
                position: absolute;
                color: #fff;
                top: -50px;
                right: 10px;
                padding: 1rem;
                background: #1414149e;
                border-radius: .5rem;
                border: 1px solid orange;
                box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;            
            }
            button {
                width: 100%;
                max-width: 200px;
            }
        }
        .comment {
            padding: .5rem;
            border-radius: .25rem;
            background: #fff;
            box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
            .author {
                font-size: .85rem;
                margin-left: 5px;
            }
        }
        
    }
}

@media screen and (max-width: 1024px) {
    #post .post-pic img {
        // min-height: 40vh;
    }
}

@media screen and (max-width: 768px) {
    #blog-page .blog-list .post .tags {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 500px) {
    #post .post-pic img {
        // min-height: 20vh;
    }
    #blog-page .blog-list .post .title {
        font-size: 3.5vw;
    }
    #blog-page .blog-list {
        grid-template-columns: 1fr;
    }
    #blog-page .blog-list .post {
        padding: .5rem;
        height: 300px;
        .tags {
            font-size: 3vw;
        }
    }

}